
.s_slide_bar {
  display: table;
  height: 100%;
  width: 100%;
}
.s_slide_bar .s_slide_container {
  display: table-cell;
  vertical-align: middle;
}
.s_slide_bar .s_slide_container .s_slide_item {
  cursor: pointer;
  width: 100%;
  height: 30px;
  line-height: 30px;
  background: rgba(0, 0, 0, 0.3);
  color: white;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  margin: 5px 0;
  padding: 0 15px;
  text-align: center;
}
.s_slide_bar .s_slide_container .s_slide_item:hover,
.s_slide_bar .s_slide_container .s_slide_item_current {
  background: rgba(255, 255, 255, 0.5);
  color: black;
}



