
.s_turnning_pic {
  position: absolute;
  width: 360px;
  height: 208px;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  margin: auto;
  border: 1px solid #ddd;
  border-raduis: 4px;
  background: rgba(0, 0, 0, 0.5);
  cursor: pointer;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  transition: left 0.6s ease-in-out, top 0.6s ease-in-out, right 0.6s ease-in-out, bottom 0.6s ease-in-out, width 0.6s ease-in-out, height 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, left 0.6s ease-in-out, top 0.6s ease-in-out, right 0.6s ease-in-out, bottom 0.6s ease-in-out, width 0.6s ease-in-out, height 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, left 0.6s ease-in-out, top 0.6s ease-in-out, right 0.6s ease-in-out, bottom 0.6s ease-in-out, width 0.6s ease-in-out, height 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
}
.s_turnning_pic .s_turnning_content {
  position: relative;
  width: 100%;
  height: 100%;
}
.s_turnning_pic .s_turnning_content .s_trunning_front,
.s_turnning_pic .s_turnning_content .s_trunning_back {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 10px;
}
.s_turnning_pic .s_turnning_content .s_trunning_front {
  z-index: 2;
}
.s_turnning_pic .s_turnning_content .s_trunning_front .s_trunning_pic_container {
  position: absolute;
  top: 10px;
  left: 10px;
  right: 10px;
  bottom: 40px;
}
.s_turnning_pic .s_turnning_content .s_trunning_front .s_trunning_pic_container img {
  width: 100%;
  height: 100%;
  background: white;
  border: 1px solid #ddd;
  -webkit-filter: grayscale(1);
  transition: all 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
.s_turnning_pic .s_turnning_content .s_trunning_front .s_trunning_pic_container img:hover,
.s_turnning_pic .s_turnning_content .s_trunning_front .s_trunning_pic_container .s_img_color {
  -webkit-filter: none;
}
.s_turnning_pic .s_turnning_content .s_trunning_front .s_turnning_pic_name {
  position: absolute;
  width: 100%;
  bottom: 10px;
  line-height: 30px;
  font-size: 12px;
  color: white;
}
.ddd {
  color: #666!important;
}



