
.s_home {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.s_home .s_section {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  -webkit-transition: top 0.5s;
}
.s_home .s_search {
  position: absolute;
  top: 40px;
  width: 100%;
  height: 120px;
}
.s_home .s_slide {
  position: absolute;
  right: 0;
  height: 100%;
  z-index: 200;
}
.s_section_item {
  width: 100%;
  height: 100%;
  background: url(../images/pic.jpg) no-repeat center;
}
.s_section_item .s_section_cover {
  position: relative;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  overflow: hidden;
}
.s_section_item .s_section_cover .s_section_author {
  position: absolute;
  right: 20px;
  bottom: 20px;
  font-size: 12px;
  color: #ddd;
}



