
.s_snow_container {
  position: absolute;
}
.s_snow_container .s_running_snow {
  position: relative;
  display: inline-block;
  margin: 20px 10px;
  width: 150px;
  height: 150px;
  cursor: pointer;
  transition: 0.6s;
  background: rgba(0, 0, 0, 0.5);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  border-radius: 4px;
}
.s_snow_container .s_running_snow .s_running_front,
.s_snow_container .s_running_snow .s_running_back {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  padding: 10px;
  color: white;
  text-align: center;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
.s_snow_container .s_running_snow .s_running_front {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
  z-index: 2;
}
.s_snow_container .s_running_snow .s_running_back {
  -webkit-transform: rotateY(-180deg);
          transform: rotateY(-180deg);
}
.s_snow_container .s_running_snow:hover {
  -webkit-animation: none;
          animation: none;
}
.s_snow_container .s_snow_animation {
  -webkit-animation: s_snow_truning 3s infinite ease-in-out;
          animation: s_snow_truning 3s infinite ease-in-out;
}
@-webkit-keyframes s_snow_truning {
0% {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
}
50% {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}
100% {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
}
}
@keyframes s_snow_truning {
0% {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
}
50% {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}
100% {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
}
}



