
.s_screen_stage {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
}
.s_screen_stage .s_screen_show {
  position: absolute;
  left: 0;
  right: 0;
  top: 170px;
  margin: auto;
  width: 800px;
  height: 400px;
  -webkit-transform: translateZ(50px);
          transform: translateZ(50px);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-perspective: 800px;
          perspective: 800px;
  transition: all 0.3s ease-in;
}
.s_screen_stage .s_screen_show img {
  width: 100%;
  height: 100%;
  padding: 30px 0;
  background: rgba(0, 0, 0, 0.6);
  box-shadow: 0 0 20px #ffffff;
}
.s_screen_stage .s_screen_box {
  position: absolute;
  right: 0;
  left: 0;
  bottom: 40px;
  height: 220px;
  margin: auto;
  text-align: center;
  transition: all 0.3s ease-in;
}
.s_screen_stage .s_screen_box .s_screen_index {
  display: none;
  width: 25px;
  height: 25px;
  line-height: 25px;
  text-align: center;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.5);
  color: white;
  margin: 150px 20px 0 5px;
  cursor: pointer;
}
.s_screen_stage .s_screen_box .s_screen_index:hover {
  background: rgba(255, 255, 255, 0.5);
  color: black;
}
.s_screen_stage .s_screen_box .s_screen_card {
  position: absolute;
  width: 180px;
  height: 220px;
  -webkit-perspective: 800px;
          perspective: 800px;
  z-index: 2;
}
.s_screen_stage .s_screen_box .s_screen_card .s_card_container {
  cursor: pointer;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-perspective: 1200px;
          perspective: 1200px;
  width: 100%;
  height: 100%;
  transition: all 0.3s ease-in;
  -webkit-transform: rotateY(-60deg);
          transform: rotateY(-60deg);
  -webkit-transform-origin: center;
          transform-origin: center;
  background: rgba(0, 0, 0, 0.5);
  padding: 10px;
}
.s_screen_stage .s_screen_box .s_screen_card .s_card_container img {
  display: block;
  width: 100%;
  height: 190px;
  border: 1px solid #ddd;
  -webkit-filter: grayscale(1);
  transition: all 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
.s_screen_stage .s_screen_box .s_screen_card .s_card_container .s_card_discription {
  width: 100%;
  height: 20px;
  color: black;
}
.s_screen_stage .s_screen_box .s_screen_card:hover {
  z-index: 33!important;
}
.s_screen_stage .s_screen_box .s_screen_card:hover .s_card_container {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
  background: white;
}
.s_screen_stage .s_screen_box .s_screen_card:hover .s_card_container img {
  -webkit-filter: none;
}
@-webkit-keyframes shining-screen {
0% {
    box-shadow: 0 0 5px rgba(255, 255, 255, 0.5);
}
50% {
    box-shadow: 0 0 15px #ffffff;
}
100% {
    box-shadow: 0 0 5px rgba(255, 255, 255, 0.5);
}
}
@keyframes shining-screen {
0% {
    box-shadow: 0 0 5px rgba(255, 255, 255, 0.5);
}
50% {
    box-shadow: 0 0 15px #ffffff;
}
100% {
    box-shadow: 0 0 5px rgba(255, 255, 255, 0.5);
}
}
@media (max-height: 840px) {
.s_screen_box {
    height: 30px!important;
}
.s_screen_box .s_card_container {
    display: none;
}
.s_screen_box .s_screen_index {
    margin-top: 0!important;
    display: inline-block !important;
}
}



