/*容器*/
.s_container {
  width: 1180px;
  margin: 0 auto;
}
/*头部*/
.s_list_header {
  width: 100%;
  height: 60px;
  line-height: 60px;
  color: white;
  background-color: rgba(0, 0, 0, 0.8);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.8);
}
.s_list_header .s_list_title {
  font-size: 22px;
}
.s_list_header .s_list_title s_title_big {
  display: inline-block;
  width: 200px;
}
.s_list_header .s_list_title .s_title_small {
  font-size: 14px;
  float: right;
}
.s_list_header .s_list_title .s_title_small a {
  display: inlint-block;
  padding: 0 10px;
  cursor: pointer;
}
/*中部*/
.s_list_container {
  min-height: 800px;
}
/*尾部*/
.s_list_footer {
  font-size: 14px;
  color: #ddd;
  padding-top: 10px;
  height: 80px;
  text-align: right;
}



