
.s_movie_state {
  position: relative;
  width: 100%;
  height: 100%;
  -webkit-perspective: 800px;
          perspective: 800px;
}
.s_movie_state .s_movie_device {
  position: absolute;
  left: 150px;
  bottom: 100px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: white;
  box-shadow: 0 0 5px #fff;
}
.s_movie_state .s_movie_screen {
  position: absolute;
  width: 100%;
  height: 300px;
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.5);
}
.s_movie_state .s_movie_cube {
  position: relative;
  width: 600px;
  height: 300px;
  -webkit-transform-origin: 0 0 200px;
          transform-origin: 0 0 200px;
  -webkit-transform: rotateY(10deg);
          transform: rotateY(10deg);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}
.s_movie_state .s_movie_cube .s_cube_side {
  position: absolute;
  border: 1px solid #ddd;
}
.s_movie_state .s_movie_cube .s_cube_side .s_container {
  position: relative;
  width: 100%;
  height: 100%;
  -webkit-perspective: 800px;
          perspective: 800px;
}
.s_movie_state .s_movie_dot {
  position: absolute;
  top: -5px;
  left: -5px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: white;
}
.s_movie_state .s_line_1,
.s_movie_state .s_line_2,
.s_movie_state .s_line_3,
.s_movie_state .s_line_4 {
  position: absolute;
  top: 0;
  height: 1px;
  background: white;
  -webkit-transform-origin: left;
          transform-origin: left;
}



