
.s_snow_outer {
  transition: 0.6s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  width: 80px;
  height: 120px;
  float: left;
  overflow: hidden;
}
.s_snow_outer .s_snow_middle {
  width: 100%;
  height: 100%;
  overflow: hidden;
  -webkit-transform: rotate(120deg);
          transform: rotate(120deg);
}
.s_snow_outer .s_snow_middle .s_snow_inner {
  width: 100%;
  height: 100%;
  overflow: hidden;
  -webkit-transform: rotate(-60deg);
          transform: rotate(-60deg);
  position: relative;
}
.s_snow_outer .s_snow_middle .s_snow_inner .s_snow_front,
.s_snow_outer .s_snow_middle .s_snow_inner .s_snow_back {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  line-height: 120px;
  text-align: center;
  color: red;
  cursor: pointer;
}
.s_snow_outer .s_snow_middle .s_snow_inner .s_snow_front {
  background: #0099ff;
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
  z-index: 2;
}
.s_snow_outer .s_snow_middle .s_snow_inner .s_snow_back {
  background: black;
  -webkit-transform: rotateY(-180deg);
          transform: rotateY(-180deg);
}
.s_snow_animation {
  -webkit-animation: s_snow_truning 5s infinite;
          animation: s_snow_truning 5s infinite;
}
@-webkit-keyframes s_snow_truning {
0% {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
}
50% {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}
100% {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
}
}
@keyframes s_snow_truning {
0% {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
}
50% {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}
100% {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
}
}
.s_snow_conttainer {
  display: inline-block;
  -webkit-perspective: 500;
  -ms-perspective: 500;
  perspective: 500;
  -ms-transform: perspective(500px);
  -moz-transform: perspective(500px);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  overflow: hidden;
  -webkit-transform: rotate(-60deg);
          transform: rotate(-60deg);
}



