
.s_music_stage {
  height: 100%;
  width: 100%;
}
.s_music_stage .s_music_container {
  position: relative;
  bottom: 5px;
  margin: auto;
}
.s_music_stage .s_music_container .s_music_item {
  position: absolute;
  bottom: 0;
  width: 20px;
  cursor: pointer;
  background: rgba(0, 0, 0, 0.5);
  transition: height 0.3s ease-in;
}
.s_music_stage .s_stage_line {
  position: absolute;
  left: 40px;
  right: 40px;
  bottom: 50px;
  height: 5px;
  border-radius: 4px;
  background: rgba(0, 0, 0, 0.5);
}



